$color-black: #1f1f1f;

body {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

.App {
	text-align: center;
}

.todo-list {
	padding: 0;
	list-style: none;
	color: $color-black;
}

.li-task {
	margin: auto;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 30vw;
}

.li-task > * {
	margin: 10px;
}

.task-form {
	margin: auto;
	display: flex;
	flex-direction: column;
	width: 30vw;
}

.task-form > * {
	margin: 10px;
}

